<style lang="sass" scoped>
  .formTile
    font-size: 16px
  .avatar
    width: 178px
    height: 178px
    display: block
</style>
<template>
  <section>
    <el-form ref="form" :model="formData" :rules="rules" label-width="80px">
      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="avatar">
        <el-upload class="avatar-uploader" :action="baseUrl + 'upload'" :show-file-list="false"
                   :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
          <img v-if="formData.avatar" :src="baseHost + formData.avatar" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="ID" prop="personId">
        <el-input v-model="formData.personId" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="age">
        <el-input v-model="formData.age" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-radio v-model="formData.sex" :label="0"></el-radio>
        <el-radio v-model="formData.sex" :label="1"></el-radio>
      </el-form-item>
      <el-form-item label="" prop="shengao">
        <el-input v-model="formData.shengao" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop=jiehun>
        <el-radio v-model="formData.jiehun" :label="0"></el-radio>
        <el-radio v-model="formData.jiehun" :label="1"></el-radio>
      </el-form-item>
      <el-form-item label="" prop="school">
        <el-input v-model="formData.school" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="xueli">
        <el-input v-model="formData.xueli" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="zhuanye">
        <el-input v-model="formData.zhuanye" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="jiangyan">
        <el-input v-model="formData.jiangyan" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="formData.phone" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="formData.email" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="zhuangtai">
        <el-input v-model="formData.zhuangtai" placeholder="" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="address">
        <el-input v-model="formData.address" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop=isShow>
        <el-radio v-model="formData.isShow" :label="0"></el-radio>
        <el-radio v-model="formData.isShow" :label="1"></el-radio>
      </el-form-item>
      <el-form-item label="" prop="xinzi">
        <el-input v-model="formData.xinzi" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="didian">
        <el-input v-model="formData.didian" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="zhiwei">
        <el-input v-model="formData.zhiwei" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="shijian">
        <el-input v-model="formData.shijian" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="leixing">
        <el-input v-model="formData.leixing" size="mini"></el-input>
      </el-form-item>
      <el-form-item label="" prop="hangye">
        <el-input v-model="formData.hangye" size="mini"></el-input>
      </el-form-item>
      <div v-for="(item, index) in formData.gongzuo">
        <span class="formTile"> - {{index + 1}} </span><el-button type="danger" size="mini" @click="formData.gongzuo.splice(index, 1)"></el-button>
        <el-form-item label="">
          <el-input v-model="item.startTime" size="mini" style="width: 120px"></el-input> -
          <el-input v-model="item.endTime" size="mini" style="width: 120px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyName">
          <el-input v-model="item.companyName" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="" prop="gangwei">
          <el-input v-model="item.gangwei" size="mini"></el-input>
        </el-form-item>
      </div>
      <el-button style="margin-left: 80px; margin: 12px 0;" size="mini" type="success" @click="formData.gongzuo.push({})"></el-button>

      <div v-for="(item, index) in formData.jiaoyu">
        <span class="formTile"> - {{index + 1}} </span><el-button type="danger" size="mini" @click="formData.jiaoyu.splice(index, 1)"></el-button>
        <el-form-item label="">
          <el-input v-model="item.startTime" size="mini" style="width: 120px"></el-input> -
          <el-input v-model="item.endTime" size="mini" style="width: 120px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="school">
          <el-input v-model="item.school" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="" prop="zhuanye">
          <el-input v-model="item.zhuanye" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="" prop="xueli">
          <el-input v-model="item.xueli" size="mini"></el-input>
        </el-form-item>
      </div>
      <el-button style="margin-left: 80px; margin: 12px 0;" size="mini" type="success" @click="formData.jiaoyu.push({})"></el-button>

      <div v-for="(item, index) in formData.peixun">
        <span class="formTile"> - {{index + 1}} </span><el-button type="danger" size="mini" @click="formData.peixun.splice(index, 1)"></el-button>
        <el-form-item label="">
          <el-input v-model="item.startTime" size="mini" style="width: 120px"></el-input> -
          <el-input v-model="item.endTime" size="mini" style="width: 120px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="kecheng">
          <el-input v-model="item.kecheng" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="" prop="jigou">
          <el-input v-model="item.jigou" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input v-model="item.content" type="textarea" size="mini"></el-input>
        </el-form-item>
      </div>
      <el-button style="margin-left: 80px; margin: 12px 0;" size="mini" type="success" @click="formData.peixun.push({})"></el-button>
    </el-form>
    <el-button style="margin-left: 80px; margin-top: 12px;" type="primary" @click="submit"></el-button>
  </section>
</template>
<script>
  import {baseUrl,baseHost} from '../config'
  import {queryOneResume, addResume, updateResume} from "../api/resume"

  export default {
    name: 'news_page',
    data() {
      return {
        baseUrl,
        baseHost,
        config: {
          initialFrameWidth: null,
          initialFrameHeight: 400
        },
        formData: {
          isShow: 0,
          sex: 0,
          jiehun: 0,
          gongzuo: [],
          jiaoyu: [],
          peixun: [],
          avatar: ''
        },
        rules: {
          title: [
            {required: true, message: '', trigger: 'blur'},
          ]
        }
      }
    },
    methods: {
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.$message.error(' JPG png gif !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      },
      handleAvatarSuccess(res, file) {
        if (res.code == 'success') {
          this.formData.avatar = "/" + res.data.url
        } else {
          this.$message.error('');
        }
      },

      submit() {

        var isPass = false
        this.$refs['form'].validate((valid) => {
          if (valid) {
            isPass = true;
          } else {
            isPass = false;
          }
        });
        if (!isPass) {
          return false;
        }

        if (this.$route.params.id) {
          updateResume(this.formData).then(e => {
            this.$router.push({name: 'resume'})
          }).catch(e => {
            this.$message.error(e);
          })
        } else {
          addResume(this.formData).then(e => {
            this.$router.push({name: 'resume'})
          }).catch(e => {
            this.$message.error(e);
          })
        }
      }
    },
    created() {
      if (this.$route.params.id) {
        queryOneResume({
          id: this.$route.params.id
        }).then(e => {
          this.formData = e;
        })
      }
    }
  }
</script>



